<template>
    <div class="before-container">

        <!-- <div class="alertview" v-if="alertFlag">
            <div class="title">
                提示
            </div>
            <div class="content">{{alertcontent}}</div>
            <button class="confirm" @click="handleAlertviewClick">确定</button>
        </div> -->
        <AlertView :arg="arg" @stateChange="handleStateChange" />
        <div class="mainbox">
            <div class="button-container" v-for="(item, index) in qList" :key="index" @click="handleClick(index,item)">
                <button  :class="{actived : index === answerIndex}" >
                    {{item}}
                    <span v-if="index === answerIndex"></span>
                </button>
            </div>

            <button class="submit" @click="handleSubmit">
                提交
            </button>
        </div>

    </div>
</template>

<script>
    import axios from 'axios'
    import common from '../common/common.js'
    import AlertView from '../common/alertView.vue'
    // import { Button, MessageBox }  from 'mint-ui'

    export default {
        props:{
            nickname: {
                type: String,
                // required: true
            },
            openId: {
                type: String,
                // required: true
            },

        },
        data(){
            return {
                qList: [
                    '1、明显是一只法牛啊。',
                    '2、应该是一只柯基啦。',
                    '3、绝对是一只贵宾犬。',
                    '4、以上答案都不正确。'
                ],
                qId: '谁是LULU?',
                answerIndex: null,
                answer: '',
                arg: {
                    alertFlag: false,
                    alertcontent: ''
                }

            }
        },
        components: {
            AlertView
        },
        computed:{

        },
        created(){
            common.bus.$emit('history', false)
        },
        mounted(){
            console.log(this.nickname, this.openId)
        },
        methods: {
            handleClick(index, item){
                this.answerIndex = index;
                this.answer = item.split('、')[1]
            },
            handleSubmit(){
                if(this.answerIndex === null){
                    this.formateArguments({
                        alertFlag: true,
                        alertcontent: '请选择答案后提交!'
                    })
                    return
                }
                let url = common.baseUrl+'/user_answer'
                axios.get(url, {
                    params: {
                        qId: this.qId,
                        answer: this.answer,
                        openId: this.openId,
                        nickname: this.nickname
                    }
                }).then((res) => {
                    /**
                     * status: 1 提交成功（新用户登录创建用户）
                     * status: 0 已经提交过，提交不成功
                     * status: 2 提交成功（用户已经登录过，更新数据）
                     */
                    if(res.data.status === 0){
                        this.formateArguments({
                            alertFlag: true,
                            alertcontent: '您已经提交过答案!',
                            callback: () => {
                                this.$router.push('/after')
                            }
                        })
                    }else{
                        this.formateArguments({
                            alertFlag: true,
                            alertcontent: res.data.message,
                            callback: () => {
                                this.$router.push('/after')
                            }
                        })
                    }
                }).catch((err) => {
                    console.log(err)
                })
            },
            formateArguments(obj){
                this.arg = Object.assign(this.arg, obj)
            },
            handleStateChange(state){
                this.arg.alertFlag = state
            }
        }

    }
</script>

<style lang='less' scoped>
    .before-container{
        width : 750px;
        height: 1136px;
        overflow: hidden;
        //http://img3.imgtn.bdimg.com/it/u=3810581282,3898894895&fm=27&gp=0.jpg
        background: url('../../assets/beforebg.jpg') no-repeat center;
        background-size: 100% 100%;
        position: relative;
        .alertview{
            width: 500px;
            background: #fff;
            box-shadow: 5px 5px 15px #888888;
            z-index: 200;
            padding: 10px 0 0px;
            border-radius: 15px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            .title{
                height: 60px;
                line-height: 60px;
                font-size: 30px;
                font-weight: bold;
            }
            .content{
                box-sizing: border-box;
                padding: 20px 35px;
                font-size: 25px;
                border-bottom: 1px solid #ccc;
                color: #999;
            }
            .confirm{
                display: inline-block;
                width: 100%;
                height: 100%;
                padding: 0 25px;
                border: 0;
                border-radius: 0 0 15px 15px;
                font-size: 30px;
                color: #26a2ff;
                height: 80px;
            }
        }

        .mainbox{
            position: relative;
            top: 465px;
            .button-container{
                margin-bottom: 15px;
                &:first-of-type{
                    margin-top: 32px;
                }
            }

            button{
                width: 580px;
                height: 100px;
                background:rgba(255, 255, 255, .5);
                color: white;
                font-size: 35px;
                padding-left: 78px;
                text-align: left;
                transform: skewY(-5deg);
                box-sizing: border-box;
                span{
                    display: inline-block;
                    margin-left: 20px;
                    width: 50px;
                    height: 36px;
                    background: url('../../assets/right.png') no-repeat center;
                    background-size: 100% 100%;
                }
            }
            .actived{
                border-bottom: 6px solid #fff;
                background:rgba(200, 205, 205, .5);
            }
            .submit{
                padding: 0;
                text-align: center;
            }
        }

    }
</style>

<style>
    .mint-msgbox{
        font-size: 30px;
    }
    .mint-msgbox-header {
        height: 60px;
        line-height: 60px;
    }
    .mint-msgbox-title{
        font-size: 30px;
    }
    .mint-msgbox-content{
        height: 80px;
    }
    .mint-msgbox-message{
        line-height: 80px;
    }
    .mint-msgbox-btns{
        height: 80px;
        line-height: 80px;
    }
    .mint-msgbox-btn{
        font-size: 30px;
    }
</style>







